import Vue from "vue";
import { Production } from "./Production";

export default new Vue({
  data() {
    return {
      product: new Production(),
      selectProduct: [],
    };
  },
  methods: {
    addProductShop(item) {
      if (this.selectProduct.every(el => el.id !== item.id)) {
        this.selectProduct.push({
          ...item,
          count: 1,
        });
      }
    },
    async shop(info) {
      await this.product.shop(this.selectProduct, info);
    }
  },
});
