import gql from "graphql-tag";
import qraphql from "@/apollo-client";

class Admin {
  template = {
    brand: {
      name: "",
    },
    product: {
      id_brand: '',
      name: '',
      is_trend: false,
      cost: 10,
      discount: 0,
      view: "",
      gender: ""
    },
    order: {
      items: [],
      info: {},
    }
  };

  updateColumn = {
    brand: Object.keys(this.template.brand),
    product: Object.keys(this.template.product),
    order: Object.keys(this.template.order),
  }

  getTemplate(name: string) {
    return JSON.parse(JSON.stringify(this.template[name]));
  }

  constructor() {}

  async loadData() {
    const response = await qraphql.query({
      query: gql`
        query {
          brand {
            id
            name
          }
          product {
            id
            ${this.updateColumn.product.join('\n')}
          }
          order(order_by: {id: desc}) {
            id
            ${this.updateColumn.order.join('\n')}
          }
        }
      `,
    });

    const { data } = response;
    return data;
  }

  async saveData(name: string, item: object) {
    const response = await qraphql.mutate({
      mutation: gql`
        mutation($objects: [${name}_insert_input!]!) {
          insert_${name}(objects: $objects, on_conflict: { constraint: ${name}_pkey, update_columns: [${this.updateColumn[name].join(',')}] }) {
            affected_rows
            returning {
              id
              ${this.updateColumn[name].join('\n')}
            }
          }
        }
      `,
      variables: {
        objects: [item],
      },
    });

    const res = response.data[`insert_${name}`].returning[0];

    return res;
  }

  async deleteData(name: string, item: any) {
    const response = await qraphql.mutate({
      mutation: gql`
      mutation($id: Int!){
        delete_${name}(where: {id: {_eq: $id}}) {
          affected_rows
        }
      }
      `,
      variables: {
        id: item.id,
      },
    });

    return response.data[`delete_${name}`].affected_rows;
  }
}

export { Admin };
