


















































import { Component, Vue } from "vue-property-decorator";
import EventBus from "@/lib/EventBus";

@Component
export default class extends Vue {
  items = EventBus.selectProduct;
  snackbar = false;
  form = {
    location: "",
    telephone: ""
  };

  add(item) {
    item.count++;
  }
  remove(item) {
    item.count--;
    if (item.count <= 0) {
      this.$delete(this.items, this.items.indexOf(item));
    }
  }

  async shop() {
    await EventBus.shop(this.form);
    this.snackbar = true;
  }
}
