import gql from "graphql-tag";
import qraphql from "@/apollo-client";

class Production {
  trendProduct = [];
  constructor() {}

  async createTrend() {
    const trend = await this.loadTrend();
    this.trendProduct = trend;
  }

  async loadTrend() {
    const response = await qraphql.query({
      query: gql`
        query {
          product(where: { is_trend: { _eq: true } }, limit: 5) {
            name
            cost
            discount
            volume
            brand {
              name
            }
          }
        }
      `
    });

    const { product } = response.data;

    return product;
  }

  async getCatalog(filter: any) {
    const response = await qraphql.query({
      query: gql`
        query{
          product {
            id
            name
            volume
            is_trend
            cost
            brand {
              name
            }
          }
        }
      `,
    });

    const { product } = response.data;
    return product;
  }

  async shop(item, info) {
    const response = await qraphql.mutate({
      mutation: gql`
        mutation($order:[order_insert_input!]!) {
          insert_order(objects: $order) {
            affected_rows
          }
        }
      `,
      variables: {
        order: [{
          items: item.map(el => ({id: el.id, count: el.count})),
          info,
        }]
      }
    });
    
    return response.data.insert_order.affected_rows;
  }
}

export { Production };
