import Vue from "vue";
import VueRouter, { RouterOptions } from "vue-router";

import Home from "@/views/Home.vue";
import ShopBag from "@/views/ShopBag.vue";
import VIndex from "@/views/VIndex.vue";
import VAdmin from "@/views/VAdmin.vue";
import VCatalog from "@/views/VCatalog.vue";
import VStartPage from "@/views/VStartPage.vue";
import VShop from "@/views/VShop.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Index",
    component: VIndex,
    children: [
      {
        path: "",
        name: "StartPage",
        component: VStartPage,
      },
      {
        path: "/catalog",
        name: "Catalog",
        component: VCatalog,
      },
      {
        path: "/shop",
        name: "Shop",
        component: VShop,
      },
    ],
  },
  {
    path: "/s_admin",
    name: "Admin",
    component: VAdmin,
  },
] as RouterOptions["routes"];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
