






































































































































































































































































import { Component, Vue } from "vue-property-decorator";
import { Admin } from "@/lib/Admin";

@Component
export default class extends Vue {
  model: Admin;
  headers = {
    brand: [
      {
        text: "Бренд",
        value: "name"
      },
      {
        text: "Действия",
        value: "actions",
        sortable: false
      }
    ],
    product: [
      {
        text: "Бренд",
        value: "brand"
      },
      {
        text: "Название",
        value: "name"
      },
      {
        text: "Это тренд?",
        value: "is_trend"
      },
      {
        text: "Вид",
        value: "view"
      },
      {
        text: "Гендер",
        value: "gender"
      },
      {
        text: "Цена",
        value: "cost"
      },
      {
        text: "Размер скидки",
        value: "discount"
      },
      {
        text: "Итоговая цена!",
        value: "costdiscount"
      },
      {
        text: "Действия",
        value: "actions",
        sortable: false
      }
    ],
    order: [
      {
        text: "Номер заказа",
        value: "id"
      },
      {
        text: "Действия",
        value: "actions",
        sortable: false
      }
    ]
  };
  options = {
    view: ["Eau de Toilette", "Eau de Parfum"].map((el, index) => ({
      text: el,
      value: index
    })),
    gender: ["Мужской", "Женский", "Унисекс"].map((el, index) => ({
      text: el,
      value: index
    }))
  };
  items = {
    brand: [],
    product: [],
    order: []
  };
  dialog = {
    brand: false,
    product: false,
    order: false
  } as any;
  loading = {
    brand: false,
    product: false,
    order: false
  } as any;
  itemEdition = {};
  isNew = false;
  optStateOrder = ["заказ в системе", "заказ получен", "заказ отправлен"];

  create(name: string) {
    this.itemEdition = this.model.getTemplate(name);
    this.isNew = true;
    this.dialog[name] = true;
  }
  async save(name: string) {
    this.loading[name] = true;
    const res = await this.model.saveData(name, this.itemEdition);
    if (this.isNew) {
      this.items[name].push(res);
    } else {
      const find = this.items[name].find(el => el.id === res.id);
      const index = this.items[name].indexOf(find);
      this.$set(this.items[name], index, res);
    }
    this.itemEdition = {};
    this.loading[name] = false;
    this.dialog[name] = false;
  }
  close(name: string) {
    this.itemEdition = {};
    this.dialog[name] = false;
  }
  editItem(name: string, item: object) {
    this.itemEdition = JSON.parse(JSON.stringify(item));
    this.isNew = false;
    this.dialog[name] = true;
  }
  async deleteItem(name: string, item: object) {
    const res = await this.model.deleteData(name, item);
    const index = this.items[name].indexOf(item);
    this.items[name].splice(index, 1);
  }

  async created() {
    this.model = new Admin();
    const data = await this.model.loadData();

    Object.keys(data).forEach(key => {
      this.items[key] = data[key];
    });
  }

  getFromName(item: any, obj: any[]) {
    return obj.find(el => el.id === item.id);
  }
  getFinalCost(item: any) {
    const { cost, discount } = item;
    return cost - cost * (discount / 100);
  }
}
